import Link from "next/link";

export default function Header() {
    return (
        <header className="navbar bg-base-100/90 backdrop-blur-md fixed top-0 w-full z-50 border-b border-base-200">
            <div className="container-wrapper navbar">
                <div className="navbar-start">
                    <div className="dropdown">
                        <div tabIndex={0} role="button" className="btn btn-ghost lg:hidden">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor" width="24" height="24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h8m-8 6h16" /></svg>
                        </div>
                        <ul tabIndex={0} className="menu menu-sm dropdown-content mt-3 z-[1] p-2 shadow bg-base-100 rounded-box w-52">
                            <li><Link href="/benefits">Benefits</Link></li>
                            <li><Link href="/ingredients">Ingredients</Link></li>
                            <li><Link href="/testimonials">Reviews</Link></li>
                            <li><Link href="/faq">FAQ</Link></li>
                            <li><Link href="/contact">Contact</Link></li>
                        </ul>
                    </div>
                    <Link href="/" className="btn btn-ghost text-xl font-display text-brand-primary">Vedic Brew</Link>
                </div>
                <div className="navbar-center hidden lg:flex">
                    <ul className="menu menu-horizontal px-1 font-medium">
                        <li><Link href="/benefits">Benefits</Link></li>
                        <li><Link href="/ingredients">Ingredients</Link></li>
                        <li><Link href="/testimonials">Reviews</Link></li>
                        <li><Link href="/faq">FAQ</Link></li>
                        <li><Link href="/contact">Contact</Link></li>
                    </ul>
                </div>
                <div className="navbar-end">
                    <a href="#buy" className="btn btn-primary text-white">Buy Now</a>
                </div>
            </div>
        </header>
    );
}
